<template>
  <div class="login">
    <header class="login-tit">
      <div class="login-container">
        <img src="../assets/images/logo.png" alt="" width="28" height="28">
      </div>
    </header>
    <div class="login-main">
      <div class="login-left">
        <h3>u-plus</h3>
        <p>卓越的管理系统</p>
      </div>
      <div class="login-right">
        <div class="loginModule">
          <h2>会员登录</h2>
          <ul>
              <li>
                <div class="field pr">
                    <label class="text name" for=""></label>
                    <input class="ipttxt" type="text" placeholder="用户名" v-model="username">
                </div>
              </li>
              <li>
                <div class="field pr">
                    <label class="text psd" for=""></label>
                    <input class="ipttxt" type="password" placeholder="密码" v-model="password" @keyup.enter="login()">
                </div>
              </li>
              <li>
                <input type="submit" class="loginBtn" v-on:click="login()" value="登录">
              </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import md5 from 'md5';
const queryString = require('querystring')
import validate from '../utils/validate.js';
export default {
        name: '',
        mixins: [validate],
        data() {
            return {
              username:'', //账户
              password:'', //密码
              validateData: {},
            }
        },
        created(){
           
        },
        mounted(){
          
        },
        computed:{

        },
        methods:{
          login(){

            this.setValidateData();
            if (!this.validateEngine(this.validateData)) return;
            var param = {
                  'account':this.username,
                  'password':md5(this.password).toLowerCase()
                };
            this.axios.post('/api/authserver/user/login', queryString.stringify(param)).then((res) => {
              if (String(res.data.code) === '200') {
                const token = res.data.data
                this.$store.commit('setToken', token)
                localStorage.setItem('token', token)
                localStorage.setItem('username', this.username)
                this.$router.push('/')
              }
            }).catch((error) => {
              console.log(error)
            });
          },
          // 校验引擎
          validateEngine: function (data) {
            for (var i = 0; i < data.length; i++) {
              //console.log(this[data[i].type]);
              if (!this[data[i].type](data[i].value)) {
                // 提示具体的错误信息
                this.$message(data[i].info);
                return false;
              }
            }
              return true;
          },
          // 配置校验数据
          setValidateData: function () {
            this.validateData = [
              {
                type: 'isNoEmpty',
                value: this.username,
                info: '账户不能为空'
              },{
                type: 'isNoEmpty',
                value: this.password,
                info: '密码不能为空'
              },
            ];
          }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
    background-color: #FAFAFA;
}
.login{
  position: relative;
  top: -60px;
}
.login-tit{
  height: 50px;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #f0f0f0;
}
.login-container {
    margin: 0 auto;
    height: 100%;
    padding: 10px 15px 0;
    text-align: center;
}
li{
  list-style: none;
}
.login-main {
  max-width: 1200px;
  height: 585px;
  display: flex;
  justify-content: center;
  margin: 80px auto 0;
}
.login-left {
  width: 20%;
}
.login-right {
  width: 40%
}

.pr { position:relative }
.loginModule {
    background: #fff;
    border: 1px solid #e1e1e1;
    padding-bottom: 20px;
    float: left;
    width:100%;
}
.loginModule h2 {
    font-size: 18px;
    font-weight: normal;
    height: 60px;
    line-height: 60px;
    margin: 0 30px 0 30px;
    padding-left: 20px;
    width: 85%;
    float: left;
    border-bottom: 1px solid #eee;
}
.loginModule ul {
    float: left;
    width: 82%
}
.loginModule ul li {
    margin: 20px 50px 0px 50px;
    font-size: 14px;
    color: #ababab;
    line-height: 24px;
}
.loginModule .field { 
    text-align: left;
    display: flex;
    align-items: center;
}
.loginModule .field input.ipttxt {
    border: 1px solid #c5c5c5;
    border-radius: 5px;
    color: #595959;
    height: 35px;
    line-height: 35px;
    font-weight: bolder;
    font-size: 14px;
    width: 90%;
    padding: 10px 5px;
    padding-left: 40px;
}

.text {
    position: absolute;
    left: 10px;
    height: 30px;
    line-height: 30px;
    width: 22px;
    color: #666;
}
.name { 
    background:url(../assets/images/input_name.jpg) no-repeat left;
}
.psd { 
    background:url(../assets/images/input_psd.jpg) no-repeat left; 
}
.loginBtn {
    background-color: #cd3e44;
    border: none;
    height: 40px;
    color: #FFF;
    font-size: 16px;
    cursor: pointer;
    width: 89%;
    text-align: center;
    line-height: 40px;
    display: inline-block;
    border-radius: 5px;
}
</style>
